<template>
  <v-app>
    <!-- Main Content -->
    <v-main class="contact-main">
      <v-container class="py-12">
        <v-card class="pa-8 mx-auto contact-card" max-width="800">
          <h2 class="text-h5 mb-6 text-center font-weight-bold text-primary">
            
          </h2>

          <v-form v-model="valid" @submit.prevent="handleSubmit">
            <v-text-field
              v-model="form.subject"
              label=""
              :rules="[(v) => !!v || '']"
              variant="outlined"
              density="comfortable"
              class="mb-4"
            />

            <v-text-field
              v-model="form.organization"
              label=""
              variant="outlined"
              density="comfortable"
              class="mb-4"
            />

            <v-text-field
              v-model="form.name"
              label=""
              :rules="[(v) => !!v || '']"
              variant="outlined"
              density="comfortable"
              class="mb-4"
            />

            <v-text-field
              v-model="form.mail"
              label=""
              :rules="[(v) => !!v || '']"
              variant="outlined"
              density="comfortable"
              class="mb-4"
            />

            <v-textarea
              v-model="form.content"
              label=""
              :rules="[(v) => !!v || '']"
              auto-grow
              variant="outlined"
              class="mb-6"
            />

            <div class="text-center">
              <v-btn
                color="primary"
                size="large"
                rounded
                type="submit"
                :disabled="!valid"
              >
                
              </v-btn>
              <Loading v-show="loading"></Loading>
            </div>
          </v-form>
        </v-card>
      </v-container>
      <v-container fluid>
        <!-- SNS Cards -->
        <v-row class="pa-2" justify="center" align="stretch" dense>
          <v-col
            cols="12"
            sm="6"
            md="4"
            v-for="sns in snsLinks"
            :key="sns.title"
          >
            <v-card
              class="mx-auto pa-2 hoverable"
              elevation="2"
              :href="sns.href"
              target="_blank"
            >
              <v-card-title class="text-subtitle-2">
                <v-icon left :color="sns.color">{{ sns.icon }}</v-icon>
                {{ sns.title }}
              </v-card-title>
              <v-card-subtitle class="text-body-2">{{
                sns.subtitle
              }}</v-card-subtitle>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-main>

    <!-- Success Dialog -->
    <v-dialog v-model="dialog" max-width="400">
      <v-card>
        <v-card-title class="text-h6 font-weight-bold text-center"
          ></v-card-title
        >
        <v-card-text class="text-center">
          <br />
          
        </v-card-text>
        <v-card-actions class="justify-center">
          <v-btn color="primary" @click="closeDialog">OK</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-app>
</template>

<script>
import axios from "axios";

export default {
  data: () => ({
    dialog: false,
    valid: false,
    form: {
      subject: "",
      organization: "",
      name: "",
      mail: "",
      content: "",
    },
    snsLinks: [
      {
        title: "X",
        subtitle: "DM",
        icon: "mdi-alpha-x-box",
        href: "https://twitter.com/KeitaKozuka",
        color: "#000000",
      },
      {
        title: "Facebook",
        subtitle: "DM",
        icon: "mdi-facebook",
        href: "https://www.facebook.com/keita.kozuka",
        color: "#3b5998",
      },
    ],
  }),
  methods: {
    async handleSubmit() {
      const entryMap = {
        subject: "entry.71885439",
        organization: "entry.60286043",
        name: "entry.1775750585",
        mail: "entry.812097791",
        content: "entry.1398375857",
      };

      const params = new URLSearchParams();
      for (const key in this.form) {
        params.append(entryMap[key], this.form[key]);
      }

      const url =
        "https://docs.google.com/forms/u/2/d/e/1FAIpQLSfJElGFNeRBlUdVLKNarXmU32rRFQ6cgV46DPqwNbHcZCrztw/formResponse?" +
        params.toString();

      try {
        await axios.get(url);
        this.form = {
          subject: "",
          organization: "",
          name: "",
          mail: "",
          content: "",
        };
        this.dialog = true;
      } catch (error) {
        console.error(":", error);
        this.dialog = true;
      }
    },
    closeDialog() {
      this.dialog = false;
    },
  },
};
</script>

<style scoped>
.contact-main {
  background: linear-gradient(to bottom, #000428, #004e92);
  min-height: 100dvh;
  color: white;
  position: relative;
}

.contact-card {
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 16px;
  backdrop-filter: blur(8px);
}
</style>
